<template>
  <div class="app_preview">
    <p class="app_preview_title">上传二维码</p>
    <div class="app_preview_list">
      <div class="app_preview_item" v-for="(item, index) in previews" :key="index">
        <img :src="item" />
        <input class="app_small_reset_selectimg" @change="inputChange" type="file">
      </div>
      <div class="app_preview_item app_preview_item_add" v-if="previews.length === 0">
        <div class="app_preview_item_wrap">
          <p class="app_preview_item_tips">点击上传</p>
          <img :src="require('../../../assets/img/appManage/upload.png')" alt />
        </div>
        <input class="app_preview_item_selectimg" @change="inputChange" type="file" />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    previews: {
      required: true
    }
  },
  methods: {
    inputChange(e) {
      console.log(e);
      var file = e.target.files[0];
      let imgBlob = this.createObject(file);
      console.log(imgBlob);
      this.previews.shift();
      this.previews.push(imgBlob);
      // 通过自定义属性将原对象带给父组件
      if (!this.previews.files) {
        this.previews.files = [];
      }
      this.previews.files.shift();
      this.previews.files.push(file);
    },

    // 生成图片地址
    createObject(blob) {
      if (window.URL) {
        return window.URL.createObjectURL(blob);
      } else if (window.webkitURL) {
        return window.webkitURL.createObjectURL(blob);
      } else {
        return null;
      }
    }
  },
  creted() {
    console.log('创建前');
    console.log(this.previews);

  }
};
</script>
<style scoped>
.app_preview {
  border-bottom: 1px solid #e6e6e6;
  padding-bottom: 50px;
}
.app_preview_title {
  padding-top: 40px;
  font-weight: bold;
}
.app_preview_list {
  display: flex;
  margin-top: 30px;
}
.app_preview_item {
  position: relative;
  margin-right: 25px;
  width: 160px;
  height: 160px;
  border: 1px solid #e6e6e6;
}
.app_preview_item img {
  width: 100%;
  height: 100%;
}
.app_preview_item_add {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #f2f2f2;
}
.app_preview_item_tips {
  color: #999999;
}
.app_preview_item_add img {
  width: 36px;
  height: 36px;
  margin-top: 15px;
}
.app_preview_item_selectimg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.app_preview_item_wrap {
  position: relative;
}
.app_small_reset_selectimg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
}
</style>

