<template>
  <div class="create_select_factory_wrap">
    <div class="create_select_factory flex">
      <div class="csf_title">选择单位</div>
      <div class="csf_select" @click="selectBtn" v-if="factoryInfo.powerList.length !== 0"></div>
      
    </div>

    <selectFactoryCorpTree :dialogShow="dialogShow" title="选择单位" @submit="submit" @reset="reset" :powerList="factoryInfo.powerList" corpTreeId="csf10" searchType="dep" />

  </div>

</template>

<script>
import selectFactoryCorpTree from './selectFactoryCorpTree'
export default {
  data() {
    return {
      dialogShow: false,
      factoryInfo: {
        powerList: [],
        title: "选择单位",
        isUser: true
      }
    }
  },

  methods: {

    selectBtn() {
      this.dialogShow = true;
      console.log(this.factoryInfo);
    },

    // 点击了确定
    submit() {
      console.log('点击了确定');
      console.log(this.factoryInfo);
      // 拿取到的部门的信息
      let selectItem = this.factoryInfo.powerList[0];
      // 将选择到的提交给父级
      this.$emit('submit', selectItem);
      this.dialogShow = false;
    },

    reset() {
      console.log('点击了取消')
    }

  },

  components: {
    selectFactoryCorpTree
  }

}
</script>
<style>
.create_select_factory {
  padding-top: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid #e6e6e6;
}
.csf_title {
  font-weight: bold;
}
.csf_select {
  margin-left: 20px;
  color: #2f78ef;
  cursor: pointer;
}
</style>
