<template>
  <div class="c_deploy">
    <el-dialog :title="title" :visible.sync="visible" @close="$emit('update:dialogShow', false)" :dialogShow="dialogShow" width="450px">
      <!-- 分页内容区域 -->
      <div class="c_dialog">
        <div class="c_dialog_left">
          <div class="c_dialog_left_top">

            <div class="c_dialog_input">
              <img class="c_dialog_icon" :src="require('../../../assets/img/power/search.png')" alt>
              <el-input placeholder="输入关键字"></el-input>
            </div>
            <el-button class="c_dialog_left_btn" icon="el-icon-plus"></el-button>
          </div>

          <!-- 树形菜单区域 -->
          <div class="c_tree_menu">
            <DepTree :corpTreeId="corpTreeId" :searchType="searchType" />
          </div>
        </div>
      </div>

    </el-dialog>
  </div>
</template>

<script>
import DepTree from "../../../components/c_departmentTree.vue";
export default {
  props: { //接收父组件传值
    dialogShow: {
      type: Boolean,
      default: false
    },
    factoryInfo: { //是否弹框
      type: Object,
      require: true
    },
    powerList: { //已选单位
      type: Array,
      default: []
    },
    title: {
      type: String,
      default: '权限管理'
    },
    corpTreeId: {
      type: String,
      default: 'aa2' // 集团树id 默认aa2
    },
    searchType: {
      type: String,
      default: 'all'
    }
  },
  data() {
    return {
      visible: this.dialogShow
    }
  },

  watch: {
    dialogShow() {
      console.log('监听到改变了？');
      this.visible = this.dialogShow;
    }
  },

  methods: {
    /**
     * 点击返回按钮给父组件传值
     */
    submitBtn() {
      this.$emit('submit');
    },
    resetBtn() { //重置按钮
      this.$emit('reset');
    },

    // 默认加载函数
    clickEmitFn(obj) {
      let { item, searchType, corpTreeId } = obj;
      console.log(item);
      if (corpTreeId === 'csf10') {
        this.powerList.shift();
        this.powerList.push(item);
        this.$emit('submit');
      }
    }
  },
  mounted() {
    // 监听点击集团树文字
    this.$zero.eventBus.$on("departmentItem", this.clickEmitFn);
  },

  beforeDestroy() {
    // 卸载监听集团树文字
    this.$zero.eventBus.$off("departmentItem", this.clickEmitFn);
  },
  components: {
    DepTree
  }
};
</script>

<style scoped>
.c_deploy .el-dialog__header {
  border-bottom: 1px solid #000;
}
.c_deploy .el-dialog__body {
  padding-top: 15px;
  padding-bottom: 55px;
}
/* 弹框内容区域 */
.c_dialog {
  width: 100%;
  height: 520px;
  display: flex;
  justify-content: space-between;
}

.c_dialog_left {
  width: 385px;
  height: 520px;
  overflow: hidden;
}

.c_dialog_left_top {
  display: flex;
  margin-bottom: 12px;
}

.c_dialog_input {
  flex-grow: 1;
  position: relative;
}

.c_dialog_icon {
  position: absolute;
  left: 15px;
  top: 12px;
  z-index: 9;
  width: 16px;
  height: 16px;
}

.c_dialog_left_btn {
  padding: 6px;
  margin-left: 10px;
  border-color: #2f78ef;
  color: #2f78ef;
  font-size: 24px;
}

/* 树形菜单区域 */
.c_tree_menu {
  width: 100%;
  height: 90%;
  background-color: #f2f2f2;
  overflow: auto;
}
</style>
