<template>
  <div class="add_new_app">
    <CContentHeader content="新建小程序" :isShowIcon="true" />

    <div class="ana_content">
      <div class="ana_content_wrap">
        <AppInfo :inputmodel="uploadInfo" />
        <SmallVersion :appinfo="uploadInfo" />
        <viewRange @allow_ranges="allow_ranges" />
        <AppPreviewImg :previews="uploadInfo.previews" />

        <SelectFactory />

        <selectLocation :radio="uploadInfo" />

        <uploadSmallQrcode :previews="uploadInfo.qrcode" />
        <AppAddBtn @add="addBtn" />

      </div>
    </div>
    <!-- 加载中……lodding框 -->
    <div class="ana_loadding">

    </div>
  </div>
</template>
<script>
import AppInfo from "./components/appInfo.vue";
import SmallVersion from './components/smallVersion'
import AppPreviewImg from "./components/appPreviewImg";
import AppAddBtn from "./components/appCreateBtn";
import selectLocation from './components/selectLocation';
import viewRange from './components/viewRange'
import uploadSmallQrcode from './components/uploadSmallQrcode'
import SelectFactory from './components/selectFactory'

// 公有方法合并
import myMixinFn from './mixinFn'
export default {
  mixins: [myMixinFn],
  data() {
    return {
      // 要上传的信息 以及一些中间结果 传递给子组件让子组件动态改变值
      uploadInfo: {
        appid: "", // 小程序的appid
        appVersion: '', // 小程序的版本号
        appName: "", // 应用名称
        appDes: "", // 应用介绍
        previews: [], // 预览图片
        typeid: '', // typeid 应用分类 (场外 户内)
        allow_ranges: [], // 允许的
        qrcode: []
      },

      // 上传过程中产生的一些网络地址字符串
      middleFilesResult: {
        logoUrl: '', // logoURL地址
        previews: [], // 预览图片地址
        qrUrl: '', // 小程序二维码地址
      }
    };
  },

  methods: {
    allow_ranges(arrAllow) {
      console.log('这里拿到选择的东西');
      console.log(arrAllow);
      /*
        可见范围传递参数结构
        [{
          "type": "dep", 
          "dep": "5d15b2ae4e5383885046950a",
          "name": "3D技术部"
          
        }, {
          "type": "user",
          "user": "5d4b93a32027b3512c00a1e4",
          "name": "测试号管理员"
        }]
      */
      // 修改允许的
      let item;
      for (let i = 0, length = arrAllow.length; i < length; i++) {
        item = arrAllow[i];
        let pushObj = {};
        if (item.type === 'dep') {
          pushObj.type = 'dep';
          pushObj.dep = item._id;
        } else if (item.type === 'user') {
          pushObj.type = 'user';
          pushObj.user = item._id;
        } else {
          // 都不满足则为 企业集团
          pushObj.type = 'dep';
          pushObj.dep = item._id;
        }
        pushObj.name = item.name;
        this.uploadInfo.allow_ranges.push(pushObj);
      }
    },

    // 上传二维码
    async uploadQrcode() {
      console.log('拿到二维码');
      console.log(this.uploadInfo);

      if (!this.uploadInfo.qrcode.files) {
        // 文件不存在
        return this.$zero.ToastError('请上传小程序二维码');
        if (this.uploadInfo.files.length === 0) {
          return this.$zero.ToastError('请上传小程序二维码');
        }
      }
      this.loadding = this.$zero.wholeLoadding({ text: '上传小程序码中...' });
      let uploadQrcodeResult = await this.mixinUploadImg(this.uploadInfo.qrcode.files);
      this.loadding.close();

      if (!uploadQrcodeResult) {
        console.log('上传小程序二维码出错了');
        return;
      }
      this.middleFilesResult.qrUrl = uploadQrcodeResult[0].file;
      this.uploadPreviewImg();
    },


    // 上传logo
    async uploadLogo() {

      if (!this.uploadInfo.imgFile) {
        return this.$zero.ToastError('请上传小程序logo');
      }


      this.loadding = this.$zero.wholeLoadding({ text: '上传logo中...' });
      let logoUrl = await this.mixinUploadImg([this.uploadInfo.imgFile]);
      this.loadding.close();
      if (!logoUrl) {
        return console.log('logo上传出错了');
      }

      console.log('url路径地址');
      console.log(logoUrl);

      this.middleFilesResult.logoUrl = logoUrl[0].file;
      // logo上传完成 上传应用的所有的一些信息
      this.createApp();
    },

    // 上传预览图
    async uploadPreviewImg() {
      console.log('获取到的图片列表');
      console.log(this.uploadInfo.previews.files);
      try {
        if (!(this.uploadInfo.previews.files && this.uploadInfo.previews.files.length !== 0)) {
          // 如果不存在 或者 这个的长度为0 直接退出
          this.$zero.ToastWarning('没有预览图片');
          // 上传logo
          this.uploadLogo();
          return;
        }
        // console.log('object')
        this.loadding = this.$zero.wholeLoadding({ text: '上传预览图中..' });
        let uploadResult = await this.mixinUploadImg(this.uploadInfo.previews.files);
        this.loadding.close();

        // 上传logo
        this.uploadLogo();

        console.log('上传预览图结果');
        console.log(uploadResult);
        if (!uploadResult) {
          return;
        }

        for (let i = 0; i < uploadResult.length; i++) {
          this.middleFilesResult.previews.push(uploadResult[i].file);
        }
        // this.middleFilesResult.previews = 
      } catch (e) {
        console.log('执行过程出错了');
        console.log(e);
      }
    },

    // 创建app应用
    async createApp() {

      console.log('上传的什么东西');
      console.log(this.uploadInfo);
      console.log('应用的链接');
      console.log(this.middleFilesResult);
      // 此时肯定有这个东西了 之前判断很多次了
      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      console.log('创建应用接口调用');
      // 此刻全部上传完成 调取上传应用的接口
      this.loadding = this.$zero.wholeLoadding({ text: '上传应用信息...' });
      let uploadResult = await this.$api.application.createSMALL({
        company_id: company_id,
        _name: this.uploadInfo.appName,
        typeid: this.uploadInfo.typeid,
        icon: this.middleFilesResult.logoUrl,
        description: this.uploadInfo.appDes,
        founder: user_id,
        manages: [user_id],
        allow_ranges: this.uploadInfo.allow_ranges,
        previews: this.middleFilesResult.previews,
        version: this.uploadInfo.appVersion,
        small: {
          qr_type: 'qr',
          qr_url: this.middleFilesResult.qrUrl
        },
        agentid: this.uploadInfo.appid
      });
      this.loadding.close();

      console.log('上传结果');
      console.log(uploadResult);
      if (uploadResult.statusCode === 75500) {
        return this.$zero.notify({
          title: '应用创建失败',
          message: `服务繁忙，上传失败，请稍后再试`,
          duration: 0
        });
      }
      if (uploadResult.statusCode !== 75200 && uploadResult.statusCode !== 75201) {
        return this.$zero.notify({
          title: '应用创建失败',
          message: `应用创建失败，错误码:${uploadResult.statusCode}`,
          duration: 0
        });
      }

      // 新建成功
      this.$zero.notify({
        title: '应用创建成功',
        message: '新建了一个小程序应用，可在控制台查看',
        duration: 0
      });
      setTimeout(() => {
        // this.$router.push('/');
        // 返回上一层
        this.$router.go(-1);
      }, 3000);
    },

    // 点击了创建应用按钮
    async addBtn() {
      console.log("点击了创建应用按钮");
      /**
       * 点击了创建的流程，后面可等待优化
       *  1. 先去单独的上传应用包 -----> 换取应用包的网络链接地址
       *  2. 上传预览图 ----> 换取预览连接地址
       *  3. 上传base64位logo ---> 换取logo地址
       *  4. 拿到上述三个东西之后，再拿去已有的信息，上传，所有信息，创建成功
       */
      // 上传qrcode
      this.uploadQrcode();
    },

  },
  components: {
    AppInfo,
    SmallVersion,
    AppPreviewImg,
    AppAddBtn,
    selectLocation,
    viewRange,
    uploadSmallQrcode,
    SelectFactory
  }
};
</script>
<style>
.add_new_app {
  height: calc(100% - 51px);
}
.ana_content {
  margin: 20px 35px;
}
.ana_content_wrap {
  padding: 0 40px;
  background: #fff;
}
.ana-cover-wrap {
  width: 100%;
  height: 100%;
}
</style>
